.country-phone-options {
  display: none;
  bottom: 40px;
  position: absolute;
  max-height: 300px;
  overflow-y: auto;
  z-index: 600;
  border: 1px solid #e3e3e3;
  border-top: none;
  width: 100%;
  padding: 20px 10px 10px 10px;
  background-color: #ffffff;
}
.country-phone-options .mCSB_scrollTools .mCSB_draggerRail {
  background-color: #e3e3e3;
}
.country-phone-options .mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar {
  width: 6px;
  background-color: #707070;
}
.country-phone-selected span {
  display: block;
  width: 38px;
  font-size: 16px;
  overflow: hidden;
}
.country-phone {
  position: relative;
  display: flex;
}
.country-phone-selected {
  position: relative;
  background-color: white;
  min-width: 35px;
  height: 40px;
  width: auto;
  background-image: url("../img/arrow-down.png");
  background-repeat: no-repeat;
  padding: 8.5px 33px;
  border: 1px solid #e3e3e3;
  font-size: 16px;
  background-position: 90% 13px;
  cursor: default;
}
.country-phone-option {
  cursor: pointer;
  background: white;
  padding: 2px 0;
}
.country-phone-option:hover,
.country-phone-option.hovered {
  background: #77cbff;
}
.country-phone-search {
  width: 100%;
  padding-left: 7px;
  padding-right: 7px;
  margin: 1px auto 20px auto;
  height: 40px;
  border: 1px solid #e3e3e3;
}
.country-phone-selected img {
  position: absolute;
  left: 15px;
  top: 15px;
}
.country-phone-option span {
  float: right;
  color: #999999;
  min-width: 66px;
  text-align: right;
}
.country-phone-option:hover span,
.country-phone-option.hovered span {
  color: #000000;
}
.country-phone-option span img {
  margin-left: 3px;
}
.flag {
  width: 16px;
  height: 11px;
  background: url("../img/flags.png") no-repeat;
}
